import { Component } from '@angular/core';
import { SmsConfig } from './sms-config.model';
import { ModalService, AlertService, gettext, Status } from '@c8y/ngx-components';
import { SmsGatewayService } from './sms-gateway.service';
import { pick } from 'lodash-es';

@Component({
  selector: 'c8y-sms-gateway',
  templateUrl: './sms-gateway.component.html'
})
export class SmsGatewayComponent {
  smsConfig: SmsConfig;
  oldConfig: SmsConfig;
  readonly smsProviders;
  readonly pageTitle;
  private readonly supportedConfigOptions;

  constructor(
    private modalService: ModalService,
    private alertService: AlertService,
    private smsGatewayService: SmsGatewayService
  ) {
    this.smsProviders = [
      { id: 'openit', name: 'OpenIT' },
      { id: 'sms77', name: 'sms77' }
    ];
    this.supportedConfigOptions = [
      'provider',
      'openit.username',
      'credentials.openit.password',
      'credentials.sms77.api.key'
    ];
    this.pageTitle = gettext('SMS provider');
  }

  async ngOnInit() {
    this.smsConfig = this.getEmptyConfig();
    this.oldConfig = this.getEmptyConfig();
    const res = await this.smsGatewayService.getProviderConfig();
    if (this.isSupportedProvider(res.data)) {
      this.smsConfig = this.getConfigurableProperties(res.data);
      this.oldConfig = this.smsConfig;
    }
  }

  getConfigurableProperties(config): SmsConfig {
    return pick(config, this.supportedConfigOptions);
  }

  isSupportedProvider(config) {
    return this.smsProviders.some(prov => prov.id === config.provider);
  }

  async saveSMSGatewayConfiguration(form) {
    const res = await this.smsGatewayService.saveProviderConfig(this.smsConfig);
    if (res && res.status !== 200) {
      const data = res.json ? await res.json() : undefined;
      this.alertService.addServerFailure({ data, res });
    } else {
      this.alertService.success(gettext('Credentials updated.'));
      Object.assign(this.oldConfig, this.smsConfig);
      form.pristine = true;
    }
  }

  async deleteSMSGatewayConfiguration() {
    await this.modalService.confirm(
      gettext('Delete credentials'),
      gettext(`You are about to delete SMS provider credentials. Deleting credentials will lock out any users with
      SMS-based two-factor authentication and deactivate the SMS-based smart rules and device communication.
      Do you want to proceed?`),
      Status.DANGER,
      { ok: gettext('Delete') }
    );
    await this.smsGatewayService.deleteProviderConfig(this.oldConfig);
    this.alertService.success(gettext('Credentials deleted.'));
    this.smsConfig = this.getEmptyConfig();
    this.oldConfig = this.getEmptyConfig();
  }

  getEmptyConfig(): SmsConfig {
    return {
      provider: undefined
    };
  }
}
