import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import {
  CoreModule,
  CommonModule,
  FormsModule,
  Route,
  NavigatorNode,
  gettext,
  HOOK_NAVIGATOR_NODES
} from '@c8y/ngx-components';
import { UpgradeModule } from '@c8y/ngx-components/upgrade';
import { SmsGatewayComponent } from './sms-gateway.component';
import { SmsGatewayService } from './sms-gateway.service';
import { SmsGatewayNavigationFactory } from './sms-gateway-navigation.factory';

const smsGatewayRoutes: Route[] = [{ path: 'smsgateway', component: SmsGatewayComponent }];

@NgModule({
  declarations: [SmsGatewayComponent],
  exports: [SmsGatewayComponent],
  imports: [
    CoreModule,
    CommonModule,
    FormsModule,
    RouterModule.forRoot(smsGatewayRoutes, { useHash: true }),
    UpgradeModule
  ],
  entryComponents: [SmsGatewayComponent],
  providers: [
    SmsGatewayService,
    { provide: HOOK_NAVIGATOR_NODES, useClass: SmsGatewayNavigationFactory, multi: true }
  ]
})
export class SmsGatewayModule {}
