import { Injectable } from '@angular/core';
import { gettext, NavigatorNode, NavigatorNodeFactory } from '@c8y/ngx-components';
import { ApplicationService } from '@c8y/client';

@Injectable()
export class SmsGatewayNavigationFactory implements NavigatorNodeFactory {
  private static readonly applicationName = 'sms-gateway';
  private nodeItem: NavigatorNode;

  constructor(private applicationService: ApplicationService) {}
  async get() {
    if (!this.nodeItem) {
      const showSMSProvider: boolean = (await this.applicationService.isAvailable(
        SmsGatewayNavigationFactory.applicationName
      )).data;

      if (showSMSProvider) {
        this.nodeItem = new NavigatorNode({
          label: gettext('SMS provider'),
          path: 'smsgateway',
          icon: 'envelope-o',
          parent: gettext('Settings'),
          priority: 1000
        });
      }
    }
    return this.nodeItem;
  }
}
